import React from 'react'
import './App1.css';

export default function TextForm(props) {
    const handleSubmitClick=()=>{
        props.showAlert("Submitted ur details","success");
    }
    return (
        <div className='contactform' style={{color: props.mode==='light'?'black':'white',style:'center'}}>
            <br></br>
            <br></br>
            <br></br>
            <h1 className="ContactHeading" style={{ color: props.mode === 'light' ? 'black' : 'white', style: 'center' }}>{props.ContactTitle}</h1>
            <div className="mb-5">
                <label htmlFor="exampleFormControlInput1" className="form-label">Email address</label>
                <input type="email" className="form-control" id="exampleFormControlInput1" placeholder="name@example.com" />
            </div>
            <div className="mb-5">
                <label htmlFor="mybox" className="form-label">Example textarea</label>
                <textarea className="form-control" id="mybox" rows="3"></textarea>
            </div>
            <button type="button" className="btn btn-primary btn-lg btn-block w-50" onClick={handleSubmitClick}>Submit</button>
        </div>
    )
}



